<template>
  <div class="white-list">
    <div class="section-wrapper">
      <div class="table-controller">
        <div class="controller-list">
          <div class="controller-item">
            <span class="controller-label">项目ID：</span>
            <Select
              class="controller-input"
              v-model="filters.bundleId"
              filterable
              placeholder="请选择项目ID"
            >
              <Option value="all">全部</Option>
              <Option v-for="item in gameList" :value="item" :key="item">{{ item }}</Option>
            </Select>
          </div>

          <div class="controller-item">
            <span class="controller-label">平台：</span>
            <Select
              class="controller-input"
              v-model="filters.platform"
              filterable
              placeholder="请选择平台"
            >
              <Option value="all">全部</Option>
              <Option v-for="item in platformList" :value="item" :key="item">{{ item }}</Option>
            </Select>
          </div>
          <div class="controller-item">
            <span class="controller-label">渠道：</span>
            <Select
              class="controller-input"
              v-model="filters.channel"
              filterable
              placeholder="请选择渠道"
            >
              <Option value="all">全部</Option>
              <Option v-for="item in channelList" :value="item" :key="item">{{ item }}</Option>
            </Select>
          </div>
          <div class="controller-item">
            <span class="controller-label">自定义ID：</span>
            <Select
              class="controller-input"
              v-model="filters.customId"
              filterable
              placeholder="请选择自定义ID"
            >
              <Option value="all">全部</Option>
              <Option v-for="item in customList" :value="item" :key="item">{{ item }}</Option>
            </Select>
          </div>
          <div class="controller-item">
            <span class="controller-label">应用版本号：</span>
            <Select
              class="controller-input"
              v-model="filters.appVersionStr"
              filterable
              placeholder="请选择应用版本号"
            >
              <Option value="all">全部</Option>
              <Option v-for="item in appList" :value="item" :key="item">{{ item }}</Option>
            </Select>
          </div>
        </div>
        <div class="new-add">
          <Button type="primary" @click="getData(true)">查询</Button>
        </div>
      </div>
      <Table :columns="columns" :data="data" :loading="tableLoading">
        <template slot-scope="{ row }" slot="action">
          <Button type="primary" style="margin-right: 5px" @click="edit(row)">编辑</Button>
          <!-- <Button type="error" @click="_delete(row.id)">删除</Button> -->
        </template>
      </Table>
      <div class="page">
        <Page
          :total="total"
          show-sizer
          show-total
          show-elevator
          @on-change="changePage"
          @on-page-size-change="changePageSize"
          :current="page"
          :page-size="pageSize"
        />
      </div>
    </div>
    <Modal
      :transfer="false"
      @on-visible-change="closeModal"
      title="编辑热更行资源"
      v-model="hotModal"
    >
      <Form ref="form" :model="form" :rules="ruleValidate" :label-width="140">
        <!-- <FormItem label="是否需要更新APP版本" prop="isForceUpdate">
          <RadioGroup  v-model="form.isForceUpdate">
            <Radio :disabled="disabledCheck" label="true">是</Radio>
            <Radio :disabled="disabledCheck" label="false">否</Radio>
          </RadioGroup>
        </FormItem> -->
        <FormItem label="资源状态" prop="status">
          <Select v-model="form.status" placeholder="请选择资源状态">
            <Option :value="-1">过时</Option>
            <Option :value="0">审核</Option>
            <Option :value="1">发布</Option>
          </Select>
        </FormItem>
        <FormItem label="最低适配应用版本号" prop="minAppVersionStr">
          <Select
            :disabled="disabledCheck"
            v-model="form.minAppVersionStr"
            placeholder="请选择资源状态"
          >
            <Option v-for="item in showAppVersion" :value="item" :key="item">{{ item }}</Option>
          </Select>
        </FormItem>
        <FormItem label="最高适配应用版本号" prop="maxAppVersionStr">
          <Select
            :disabled="disabledCheck"
            v-model="form.maxAppVersionStr"
            placeholder="请选择资源状态"
          >
            <Option v-for="item in showAppVersion" :value="item" :key="item">{{ item }}</Option>
          </Select>
        </FormItem>
        <FormItem label="请求超时时间（秒）" prop="timeout">
          <InputNumber :min="0" :disabled="disabledCheck" v-model="form.timeout"></InputNumber>
        </FormItem>
      </Form>
      <template slot="footer">
        <Button @click="hotModal = false">取消</Button>
        <Button type="success" @click="save" :loading="loading">确定</Button>
      </template>
    </Modal>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import {
  getList, getCurrentDetail, editRequest, checkStatus,
} from '@/api/hotUpdate';

const STATUS_MAP = {
  '-1': '过时',
  0: '审核',
  1: '发布',
};

export default {
  data() {
    return {
      columns: [
        {
          title: '项目ID',
          key: 'bundleId',
          align: 'center',
        },
        {
          title: '平台',
          key: 'platform',
          align: 'center',
        },
        {
          title: '渠道',
          key: 'channel',
          align: 'center',
        },
        {
          title: '自定义ID',
          key: 'customId',
          align: 'center',
        },
        {
          title: 'Unity版本号',
          key: 'unityVersion',
          align: 'center',
        },
        {
          title: '应用版本号',
          key: 'appVersionStr',
          align: 'center',
        },
        // {
        //   title: '是否需要更新APP版本',
        //   key: 'isForceUpdate',
        //   align: 'center',
        //   render: (h, params) => h('span', params.row.isForceUpdate ? '是' : '否'),
        // },
        {
          title: '最低适配应用版本号',
          key: 'minAppVersionStr',
          align: 'center',
        },
        {
          title: '最高适配应用版本号',
          key: 'maxAppVersionStr',
          align: 'center',
        },
        {
          title: '地区',
          key: 'area',
          align: 'center',
        },
        {
          title: '资源状态',
          key: 'status',
          align: 'center',
          render: (h, params) => h('span', STATUS_MAP[params.row.status]),
        },
        {
          title: '资源版本号',
          key: 'resourceVersion',
          align: 'center',
        },
        {
          title: '请求超时时间（秒）',
          key: 'timeout',
          align: 'center',
        },
        {
          title: '操作',
          width: 200,
          slot: 'action',
          align: 'center',
        },
      ],
      filters: {
        bundleId: 'all',
        platform: 'all',
        channel: 'all',
        customId: 'all',
        appVersionStr: 'all',
      },
      data: [],
      page: 1,
      pageSize: 10,
      total: 0,
      hotModal: false,
      loading: false,
      form: {
        area: '',
        // isForceUpdate: '',
        maxAppVersionStr: '',
        minAppVersionStr: '',
        status: 0,
        timeout: 30,
      },
      ruleValidate: {
        area: [{ required: true, message: '请输入地区', trigger: 'blur' }],
        timeout: [{ required: true, message: '请输入请求超时时间' }],
      },
      tableLoading: false,
      appVersion: [],
      //   isShow: false,
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData(isFirst = false) {
      if (isFirst) {
        this.page = 1;
      }
      this.tableLoading = true;
      getList({
        page: this.page,
        size: this.pageSize,
        bundleId: this.filters.bundleId === 'all' ? '' : this.filters.bundleId,
        platform: this.filters.platform === 'all' ? '' : this.filters.platform,
        channel: this.filters.channel === 'all' ? '' : this.filters.channel,
        customId: this.filters.customId === 'all' ? '' : this.filters.customId,
        appVersionStr: this.filters.appVersionStr === 'all' ? '' : this.filters.appVersionStr,
      })
        .then(({ list, total }) => {
          this.data = list;
          this.total = total;
          this.tableLoading = false;
        })
        .catch(() => {
          this.tableLoading = false;
        });
    },
    changePage(val) {
      this.page = val;
      this.getData();
    },
    changePageSize(val) {
      this.pageSize = val;
      this.getData();
    },
    // _delete(id) {
    //   this.$Modal.confirm({
    //     title: '删除热更新资源',
    //     content: '<p>确认删除此热更新资源？</p>',
    //     loading: true,
    //     onOk: () => {
    //       deleteRequest(id)
    //         .then(() => {
    //           this.$Modal.remove();
    //           this.getData();
    //           this.$Message.success('删除成功');
    //         })
    //         .catch(() => {
    //           this.$Modal.remove();
    //         });
    //     },
    //   });
    // },
    save() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          const maxAppVersionNum = this.appVersionMap[this.form.maxAppVersionStr];
          const minAppVersionNum = this.appVersionMap[this.form.minAppVersionStr];
          if (maxAppVersionNum < minAppVersionNum) {
            this.$Message.error({
              content: '最高适配应用版本号不能低于最低适配应用版本号',
              duration: 3,
            });
            return;
          }
          this.loading = true;
          editRequest({
            ...this.form,
            // isForceUpdate: this.form.isForceUpdate === 'true',
            maxAppVersionNum,
            minAppVersionNum,
          })
            .then(() => {
              this.loading = false;
              this.getData();
              this.hotModal = false;
              this.$Message.success('编辑成功');
            })
            .catch(() => {
              this.loading = false;
            });
        }
      });
    },
    closeModal(isShow) {
      if (!isShow) {
        setTimeout(() => {
          this.form = {
            area: '',
            // isForceUpdate: '',
            maxAppVersionStr: '',
            minAppVersionStr: '',
            status: 0,
            timeout: 30,
          };
        }, 100);
      }
    },
    edit(row) {
      this.form = { ...row };
      // this.form.isForceUpdate = this.form.isForceUpdate ? 'true' : 'false';
      getCurrentDetail({ ...row }).then((res) => {
        this.appVersion = res;
        this.hotModal = true;
      });
    },
  },
  computed: {
    ...mapState(['gameList', 'searchTree']),
    showAppVersion() {
      return this.appVersion.reduce((pre, cur) => [...pre, Object.keys(cur)[0]], []);
    },
    appVersionMap() {
      return this.appVersion.reduce((pre, cur) => {
        const key = Object.keys(cur)[0];
        pre[key] = cur[key];
        return pre;
      }, {});
    },
    platformList() {
      const { bundleId } = this.filters;
      if (bundleId === 'all') {
        return [];
      }
      return Object.keys(this.searchTree[bundleId]);
    },
    channelList() {
      const { bundleId, platform } = this.filters;
      if (bundleId === 'all' || platform === 'all') {
        return [];
      }
      return Object.keys(this.searchTree[bundleId][platform]);
    },
    customList() {
      const { bundleId, platform, channel } = this.filters;
      if (bundleId === 'all' || platform === 'all' || channel === 'all') {
        return [];
      }
      return Object.keys(this.searchTree[bundleId][platform][channel]);
    },
    appList() {
      const {
        bundleId, platform, channel, customId,
      } = this.filters;
      if (bundleId === 'all' || platform === 'all' || channel === 'all' || customId === 'all') {
        return [];
      }
      return Object.keys(this.searchTree[bundleId][platform][channel][customId]);
    },
    disabledCheck() {
      return this.form.status === 1;
    },
  },
  watch: {
    'filters.bundleId': {
      handler() {
        this.filters = {
          ...this.filters,
          platform: 'all',
          channel: 'all',
          customId: 'all',
          appVersionStr: 'all',
        };
      },
    },
    'filters.platform': {
      handler() {
        this.filters = {
          ...this.filters,
          channel: 'all',
          customId: 'all',
          appVersionStr: 'all',
        };
      },
    },
    'filters.channel': {
      handler() {
        this.filters = {
          ...this.filters,
          customId: 'all',
          appVersionStr: 'all',
        };
      },
    },
    'filters.customId': {
      handler() {
        this.filters = {
          ...this.filters,
          appVersionStr: 'all',
        };
      },
    },
    'form.status': {
      handler(newVal, oldVal) {
        if (oldVal === 1 && this.hotModal) {
          this.loading = true;
          checkStatus(this.form.id).then((res) => {
            this.loading = false;
            if (!res) {
              this.$Message.warning({
                content: '当前资源发布状态修改后，app版本下没有发布状态的资源，请谨慎操作',
                duration: 3,
              });
            }
          });
        }
      },
    },
  },
};
</script>

<style lang="less" scoped>
.white-list {
  .table-controller {
    margin-bottom: 10px;
  }
  .controller-input {
    width: 70% !important;
  }
  .page {
    display: flex;
    margin-top: 10px;
    justify-content: flex-end;
  }
  .ivu-modal {
    .info {
      position: relative !important;
      margin-left: -50px !important;
      font-weight: 600;
      color: red;
    }
  }
}
</style>
